.matador-portal {

  &-header {
    padding: 0 0 2rem 0;
  }

  &-headline {
    margin-bottom: 2rem;
  }

  &-body-container {
    @media screen and (min-width: 768px) {
      display: grid;
      grid-auto-columns: 3fr 1fr;
      grid-gap: 2rem;

      .sidebar-left & {
        grid-auto-columns: 1fr 3fr;
      }
    }

    .matador-portal-body {
      grid-column: 1;

      .sidebar-left & {
        grid-column: 2;
      }

      .matador-job-title {
        margin: 0 0 1rem 0;
      }

      .matador-job-description {
        margin: 1rem 0;
      }

      .matador-job-footer {
        .matador-job-navigation {
          padding: 0;
        }

        .matador-button {
          margin: 0;
        }
      }
    }

    .matador-portal-aside {
      grid-column: 2;

      .sidebar-left & {
        grid-column: 1;
        grid-row: 1;
      }

      ul {
        padding: 0;
        margin-left: 0;
      }

      .matador-search-form {
        @media only screen and ( min-width: 600px ) {
          flex-flow: column;
        }
      }
    }
  }

  &-aside {
    *:first-child {
      margin-top: 0;
    }

    > * {
      margin-bottom: 1rem;
    }
  }

  &-footer {
    > * {
      margin-bottom: 1rem;
    }
  }
}