.matador-search-form {
  display: flex;
  flex-flow: column;
  align-items: stretch;
  gap: 16px;

  label {
    margin: 0;
  }

  button {
    margin: 0;
    height: 100%;
  }

  @media only screen and ( min-width: 600px ) {
    flex-flow: row nowrap;
    &-field-group {
      flex: 2;
    }
    &-field-submit {
      flex: 1;
    }
  }

  input[type=text],
  select {
    width: 100%;
    box-sizing: border-box;
  }

}