.matador-application,
.matador-form {
  box-sizing: border-box;
  margin-top: 2rem;
  margin-bottom: 4rem;
  position: relative;

  .single-matador-job-listings & {
    border-top: 1px solid #eee;
    margin-top: 2rem;
    padding-top: 2rem;
    border-bottom: 1px solid #eee;
    margin-bottom: 2rem;
    padding-bottom: 2rem;
  }

  p,
  label {
    margin: 0;
    padding: 0;
  }

  ul, ol {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  ul li,
  ol li {
    margin: 0;
    padding: 0 0 .5rem 0;
  }

  select {
    width: 100%;
    height: 6em;
    font-size: 1.25rem;
  }

  input[type='text'],
  input[type='email'],
  input[type='password'],
  input[type='tel'],
  input[type='date'],
  textarea,
  select:not([multiple]) {
    padding: .5rem;
    margin: 0;
    width: 100%;
    height: 2.4em;
    font-size: 1.25rem;
  }

  input[type='radio'],
  input[type='checkbox'] {
    width: auto;
  }

  input[type='radio'] {
    -webkit-appearance: radio;
  }

  input[type='checkbox'] {
    -webkit-appearance: checkbox;
  }

  textarea {
    min-height: 180px;
  }

  input.error:not([type='submit'],[type='reset']) {
    border: 1px solid $error-color;
  }

  .matador-field-group {
    padding-top: .5rem;
    padding-bottom: 1rem;
  }

  .matador-field-group .matador-field-label {
    margin: 0 0 .5rem;
  }

  .matador-field-group .matador-label label {
    line-height: 1.25rem;
  }

  .matador-required .matador-label label:after {
    content: ('*');
    color: $error-color;
    position: relative;
    font-size: 1.25rem;
  }

  .error {
    font-weight: normal;
    margin-top: .5rem;
    font-style: italic;
    color: $error-color;
  }

  // This is the honeypot field
  .matador-field-job-seeker {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    margin: 0;
    padding: 0;
    border: 0;
    word-wrap: normal !important;
  }

  .matador-field-type-checkbox,
  .matador-field-type-radio {

    label {
      padding-left: 1.5rem;
      position: relative;
      font-weight: normal;
    }

    input {
      position: absolute;
      left: 0;
      top: .25rem;
    }
  }

  .matador-field-type-file,
  .matador-field-type-files {
    .for-file {
      font-size: 16px;
      color: #666;
      border: 1px solid #bbb;
      border-radius: 3px
    }
  }

  .matador-field-privacy_policy_opt_in {
    margin: .5rem 0 1rem;
    padding: 1rem 0;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
  }

  #matador-submit {
    margin-top: 1rem;
  }

  #matador-upload-overlay {
    .matador-upload-overlay-background {
      -moz-border-radius: 8px;
      -webkit-border-radius: 8px;
      border: 0 solid $error-color;
    }
    .matador-upload-overlay-message {
      -moz-border-radius: 8px;
      -webkit-border-radius: 8px;
      background: #fff;
    }
  }
}